import React from "react"
import { Link } from "gatsby"

const Header = () => (
    <header>
    <div
      style={{
        // margin: `0 auto`,
        maxWidth: 1200,
        padding: `1.0rem 1.0875rem`,
      }}
    >
      <h2 style={{ margin: 0 }}>
        <Link
          to="/"
          style={{
            color: `white`,
            textDecoration: `none`,
          }}
        >
          Gian Winckler
        </Link>
      </h2>
    </div>
  </header>
)

export default Header